.adv-info {
	height:379px;
	display: flex;
	flex-direction:column;
	align-items:center;
	justify-content:space-between;
	box-sizing:border-box;
	margin:190px 0 0 0;
	padding:0 0 0 110px;

	@media (max-width:$tablet-width) {
		margin:60px 0 0 0;
		padding:0;
	}
}
.adv-info__name {
	width:412px;
	font-weight: 600;
	font-size:55px;
	line-height: 80px;
	letter-spacing: -1.10667px;
	margin:0;

	@media (max-width:$tablet-width) {
		font-size: 30px;
		line-height: 45px;
	}
}
.adv-info__text {
	width:376px;
	font-size:16px;
	line-height: 27px;
	letter-spacing: -0.466667px;
	margin:-5px 0 0 0;

	@media (max-width:$tablet-width) {
		font-size: 16px;
		line-height: 170%;
		text-align:center;
	}
}
.adv-info__link {
	position: relative;
	width:340px;
	height:75px;
	display: flex;
	justify-content:flex-start;
	align-items:center;
	background:$button__accent;
	color:#fff;
	box-sizing:border-box;
	border:none;
	outline:none;
	padding:0 0 0 40px;
	margin:5px 0 0 0;
	letter-spacing: 2px;
	text-transform: uppercase;
	font-weight:700;
	font-size:12px;

	&:hover {
		cursor: pointer;
	}
	&:after {
		content:"";
		position: absolute;
		right:40px;
		top:30px;
		width:14px;
		height:14px;
		background-image: url('../../img/arrow.svg');
		background-size: 14px 14px;
		background-repeat: no-repeat;
	}

	@media (max-width:$tablet-width) {
		width:250px;
		height:50px;
		font-size: 10px;
		line-height: 12px;

		&:after {
			top:15px;
			right:25px;

		}

	}
}